const H2 = (props: any) => {
  const { children, sub }: { children: React.ReactNode; sub: string } = props;

  return (
    <Typography
      variant='h2'
      mr={1}
      p={0}
      lineHeight={1.5}
      sx={{
        '&::before': {
          content: `"${sub}"`,
          display: 'block',
          color: 'primary.main',
          mb: -1,
          top: 0,
          left: 0,
          fontSize: '1rem',
          letterSpacing: 2,
          lineHeight: 1
        }
      }}
    >
      {children}
    </Typography>
  );
};

const num = (n: number): string => {
  const map: any = {
    1: '①',
    2: '②',
    3: '③',
    4: '④',
    5: '⑤'
  };
  return map[n] || '';
};

const Dot = ({ children }: { children: React.ReactNode }) => (
  <Box
    component='span'
    sx={{
      backgroundImage:
        'radial-gradient(circle at center, orange 20%, transparent 20%)',
      backgroundPosition: 'top right',
      backgroundRepeat: 'repeat-x',
      backgroundSize: '1em 0.3em',
      paddingTop: '0.1em'
    }}
  >
    {children}
  </Box>
);

const Transition = ({
  children,
  delay = 0
}: {
  children: React.ReactNode;
  delay?: number;
}) => {
  const { ref, inView } = useInView({
    // 画面の上下中央を inview の基準線にする
    rootMargin: '-25% 0px',
    threshold: 0,
    triggerOnce: true
  });

  return (
    <Box
      ref={ref}
      sx={{
        width: '100%',
        height: '100%'
      }}
    >
      <Box
        sx={{
          width: '100%',
          height: '100%',
          transition: [
            `opacity 1000ms ease-out ${delay}ms`,
            `transform 1000ms ease-out ${delay}ms`
          ].join(','),
          transform: `translateX(${inView ? 0 : 100}px)`,
          opacity: inView ? 1 : 0
        }}
      >
        {children}
      </Box>
    </Box>
  );
};

const Flow = ({
  Image,
  title,
  description,
  showArrowIcon = false
}: {
  Image: any;
  title: string;
  description: string;
  showArrowIcon?: boolean;
}) => (
  <Box
    display='flex'
    minHeight={{
      lg: 180,
      xs: 'auto'
    }}
    flexDirection={{
      lg: 'row',
      xs: 'column'
    }}
  >
    <Box display='flex' justifyContent='center' alignItems='center'>
      {showArrowIcon && (
        <ArrowRightIcon
          sx={{
            color: 'primary.main',
            fontSize: 48,
            transform: {
              lg: 'rotate(0deg)',
              xs: 'rotate(90deg)'
            }
          }}
        />
      )}
    </Box>
    <Box
      bgcolor='background.paper'
      sx={{
        p: 2,
        width: {
          lg: 180,
          xs: '100%'
        }
      }}
    >
      <Box
        display='flex'
        flexDirection={{
          lg: 'column',
          xs: 'row'
        }}
      >
        <Box display='flex' justifyContent='center' mb={2}>
          <img src={Image} height={60} style={{ objectFit: 'contain' }} />
        </Box>
        <Typography align='center'>{title}</Typography>
      </Box>
      <Typography>{description}</Typography>
    </Box>
  </Box>
);

